<template><section><h2>快速上手</h2>
<h3>引入Ruler</h3>
<p>引入Ruler只需在main.js中写入以下内容</p>
<p>import Vue from 'vue';</p>
<br>
<p>import Ruler from 'ruler-ui';</p>
<br>
<p>Vue.use(Ruler)</p>
<br>
引入后即可在任意组件内使用ruler，比如使用简单的按钮
<coding title="简单使用组件" :code="baseCode">
  <ru-button type="primary">按钮</ru-button>
</coding>
</section></template>

<script>
export default {
  data () {
    return {
      baseCode: '<ru-button type="primary">按钮</ru-button>'
    }
  }
}
</script>


